.Favorite {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-height: 100%;
  h2 {
    margin: 20px;
  }
max-height: 100vh;
  .empty {
   height: 100vh;
  }

  button {
    margin-bottom: 50px;
  }

  .FavoriteList {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    margin-bottom: 40px;

    .Favorite__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 350px;
      height: 380px;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
      border-radius: 30px;
      padding: 10px;

      p {
        font-weight: 600;
        font-size: 17px;
        line-height: 21px;
        color: #1c1c27;
      }

      img {
        max-height: 180px;
        margin-bottom: 30px;
      }
      .price {
        display: flex;
        flex-direction: row;

        p {
          margin-left: 20px;
        }
        margin-bottom: 25px;
      }
      .rating {
        width: 150px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;

        .trash {
          margin-left: 50%;
          cursor: pointer;
        }
        .trash:hover {
          opacity: 0.7;
        }
        .star {
          width: 22px;
          height: 20px;
          margin-bottom: 0;
        }
        i {
          background: #ffce7f;
        }
        p {
          color: #838383;
          font-style: normal;
          font-weight: 600;
          font-size: 17px;
          line-height: 21px;
          margin-left: 10px;
        }
      }
    }
  }
}
