.Ordering {
  display: flex;
  flex-direction: column;
  margin-bottom: 60px;
  h2 {
    text-align: center;
    margin-bottom: 30px;
  }
  .row {
    display: flex;
    flex-direction: row;
  }
  i {
    cursor: pointer;
  }

  .delivery {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    width: 450px;
    max-height: 530px;
    padding: 10px;

    .delivery__title {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-around;
      margin-bottom: 20px;
    }

    img {
      max-width: 380px;
    }

    .adress {
      .adress__title {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        margin: 20px;
        h4 {
          margin-left: 5px;
        }
      }
      .input__row {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        .input {
          max-width: 172px;
        }
      }
    }
  }

  .input {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background: #d6d6d6;
    border-radius: 15px;
    height: 45px;
    margin-bottom: 10px;
    width: 350px;
    p {
      font-size: 18px;
      color: #838383;
    }
  }

  input {
    font-size: 18px;
    width: 70%;
    padding: 5px;
    border: none;
    background-color: transparent;
  }

  .payment {
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    .row {
      align-items: center;
    }
    .payment_box {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 450px;
      min-height: 150px;
      max-height: 800px;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
      border-radius: 30px;
      padding: 25px;
      margin-bottom: 20px;
      
      .payment_box__item {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 10px;
      }
    }
  }
}

.SuccessOrdering {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin: 40px;
  .Message {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 730px;
    height: 95px;
    background: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 30px;
    padding: 20px;
    margin-bottom: 40px;
  }
}

button {
  width: 450px;
  height: 45px;
}

